@media screen and (min-device-width: 320px) and (max-device-width: 991px) {
    .manubar {
        position: relative;
    }

    .hello {
        position: absolute;
        width: 250px;
        left: -300px;
        top: 40px;
        z-index: 1;
        transition: all 0.5s;
    }

    #chkicon {
        position: absolute;
        right: 6px;
        z-index: 1;
        width: 40px;
        height: 30px;
        opacity: 0;
        cursor: pointer;
    }

    .btnicon {
        position: absolute;
        right: 10px;
        z-index: 0;
        cursor: pointer;
    }

    #chkicon:checked~.hello {
        left: 0px;
    }


}




